body {
  font-family: "BloggerSans", "Arial", sans-serif;
  color: #ffffff;
  min-width: 320px;
  position: relative;
}
img {
  width: 100%;
  height: auto;
}
a {
  cursor: pointer;
}
a:active,
a:focus {
  outline: 2px solid #81b3d2;
}
input {
  cursor: pointer;
}
input:active,
textarea:active,
input:focus,
textarea:focus {
  outline: 2px solid #81b3d2;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden;
}
button {
  cursor: pointer;
}
button:active,
button:focus {
  outline: 2px solid #81b3d2;
}
.button-link {
  font-size: 18px;
  line-height: 18px;
  font-family: "BloggerSans-Bold", "Arial", sans-serif;
  text-decoration: none;
  color: #1d2e5b;
  display: block;
  padding: 22px 24px;
  background-color: #ffd74b;
  border-radius: 10px;
  position: relative;
}
.button-link:hover {
  background-color: #ffbd30;
}
.button-link:active {
  color: rgba(29, 46, 91, 0.3);
}
.button-link:active::before {
  opacity: 0.3;
}
.button-link::before {
  content: "";
  display: block;
  width: 10px;
  height: 14px;
  background-image: url(../../img/triangle-arrow-right.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 24px;
  right: 24px;
}
@media (min-width: 768px) {
  .button-link {
    font-size: 24px;
    line-height: 24px;
    padding: 28px 35px;
    border-radius: 20px;
  }
  .button-link::before {
    content: "";
    top: 34px;
    right: 36px;
  }
}
.page-header {
  background-color: #e2e5f2;
}
.page-header__wrapper-alignment {
  background-color: #161c35;
}
.page-header__wrapper-logo {
  font-size: 0;
  box-sizing: border-box;
  padding: 28px 25px 26px;
  display: block;
}
.page-header__logo {
  width: 100px;
  height: 19px;
}
.page-header__nav {
  width: 100%;
  position: absolute;
  z-index: 1;
}
.page-header__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.page-header__button {
  width: 26px;
  height: 26px;
  background-color: transparent;
  border: none;
  position: absolute;
  top: -50px;
  right: 24px;
}
.page-header--opened .page-header__wrapper-logo {
  background-color: #ffffff;
}
.page-header--opened .page-header__dark-theme {
  display: none;
}
.page-header--opened .page-header__button::before,
.page-header--opened .page-header__button::after {
  content: "";
  display: block;
  width: 26px;
  height: 2px;
  background-color: #cccccc;
  position: absolute;
  top: 13px;
  right: 0;
}
.page-header--opened .page-header__button::before {
  transform: rotate(45deg);
}
.page-header--opened .page-header__button::after {
  transform: rotate(-45deg);
}
.page-header--opened .page-header__nav {
  background-color: #ffffff;
}
.page-header--closed .page-header__wrapper-logo {
  background-color: #161c35;
}
.page-header--closed .page-header__light-theme {
  display: none;
}
.page-header--closed .page-header__button::after {
  content: "";
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff;
  position: absolute;
  top: 9px;
  right: 1px;
  display: block;
}
.page-header--closed .main-menu,
.page-header--closed .contacts {
  display: none;
}
.nojs .page-header__button {
  display: none;
}
.nojs .page-header__nav {
  position: relative;
}
.page-header--inner-page .page-header__wrapper-alignment {
  border-radius: 20px;
}
.page-header--inner-page .page-header__wrapper-logo {
  padding: 19px 25px 26px;
}
.page-header--inner-page .page-header__title {
  font-size: 36px;
  line-height: 40px;
  font-family: "BloggerSans-Bold", "Arial", sans-serif;
  padding: 3px 25px 12px;
  margin-bottom: 0;
  position: relative;
}
.page-header--inner-page .page-header__title::after {
  content: "";
  display: block;
  width: 173px;
  height: 5px;
  border-radius: 2.4px;
  background-color: #ff8d30;
  position: absolute;
  bottom: -2px;
}
.page-header--inner-page .page-header__link {
  display: none;
}
@media (min-width: 768px) {
  .page-header {
    background-color: #182044;
    min-height: 132px;
  }
  .page-header__wrapper-logo {
    padding: 47px 40px 42px 40px;
  }
